<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>浏览器配置</span>
      </div>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-switch
            style="display: block"
            v-model="status"
            active-color="#13ce66"
            inactive-color="gray"
            active-text="无头模式"
            inactive-text="界面模式"
            @change="updateBrowserValue"
          >
          </el-switch>
        </el-col>
        <el-col :span="8">
          <el-switch
            style="display: block"
            v-model="languageStatus"
            active-color="#13ce66"
            inactive-color="gray"
            active-text="中文"
            inactive-text="英文"
            @change="updateLanguageValue"
          >
          </el-switch>
        </el-col>
        <el-col :span="8">
          <div>
            <el-input
              placeholder="例如:1920,900"
              v-model="windowSize"
              style="width: 250px"
              size="mini"
              @change="updateWindowSize"
            >
              <template slot="prepend">浏览器尺寸</template>
            </el-input>
          </div>
        </el-col>
      </el-row>
    </el-card>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>调试模式</span>
      </div>
      <el-switch
        style="display: block"
        v-model="debugStatus"
        active-color="#13ce66"
        inactive-color="gray"
        active-text="开启"
        inactive-text="关闭"
        @change="updateDebugHub"
      >
      </el-switch>
    </el-card>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>企微消息推送</span>
        <el-tooltip
          content="可配置多个企微机器人的webHookUrl,多个webHookUrl以英文','分割"
          placement="top"
          @click.stop.prevent
        >
          <i class="el-icon-question" />
        </el-tooltip>
      </div>
      <el-form
        :inline="true"
        :model="messageConfig"
        class="demo-form-inline"
        style="margin-left: 10px"
        :rules="rules"
        ref="messageConfig"
      >
        <el-row :gutter="20">
          <el-col :span="6">
            <el-form-item label="是否开启" prop="isUse">
              <el-select
                v-model="messageConfig.isUse"
                clearable
                placeholder="请选择是否开启"
                size="mini"
                style="width:150px"
                @change="updateMessageConfig"
              >
                <el-option
                  v-for="item in isUseOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>

          <el-col :span="12">
            <el-form-item label="webHookUrl" prop="webHookUrl">
              <el-input
                type="textarea"
                placeholder="https://qyapi.weixin.qq.com/cgi-bin/webhook/send?key=4687dce0-02e5-4238-8404-aecb71beb083,多个地址以英文','分割"
                v-model="messageConfig.webHookUrl"
                style="width: 430px"
                size="mini"
                @change="updateMessageConfig"
                :autosize="{ minRows: 2, maxRows: 4 }"
                clearable
              >
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="触发条件" prop="trigger">
              <el-select
                v-model="messageConfig.trigger"
                clearable
                placeholder="请选择触发条件"
                size="mini"
                style="width:150px"
                @change="updateMessageConfig"
              >
                <el-option
                  v-for="item in triggerOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'WebUiConfig',
  data() {
    return {
      clientIp: '',
      browserValue: '1',
      status: true,
      languageStatus: true,
      languageValue: '1',
      debugStatus: false,
      windowSize: '',
      messageConfig: { isUse: '', webHookUrl: '', trigger: '' },
      triggerOptions: [
        { value: '0', label: '执行完毕' },
        { value: '1', label: '执行失败' }
      ],
      isUseOptions: [
        { value: '0', label: '关闭' },
        { value: '1', label: '开启' }
      ],
      rules: {
        isUse: [
          { required: true, message: '请选择是否开启', trigger: 'change' }
        ],
        webHookUrl: [
          { required: true, message: '请输入webHookUrl', trigger: 'change' }
        ],
        trigger: [
          { required: true, message: '请选择触发条件', trigger: 'change' }
        ]
      }
    };
  },
  methods: {
    getWindowSize() {
      let url = String.format(
        '{0}/config/getWindowSize',
        this.GLOBAL.WEBUI_URL
      );
      this.axios
        .get(url)
        .then(response => {
          this.windowSize = response.data.data.windowSize;
          return response;
        })
        .catch(response => {
          return response;
        });
    },
    updateWindowSize() {
      let config = {
        headers: {
          'Content-Type': 'application/json;charset=UTF-8'
        }
      };
      let url = String.format(
        '{0}/config/updateWindowSize',
        this.GLOBAL.WEBUI_URL
      );
      this.axios
        .post(url, JSON.stringify({ windowSize: this.windowSize }), config)
        .then(response => {
          this.getWindowSize();
          return response;
        })
        .catch(response => {
          return response;
        });
    },
    getBrowserValue() {
      let url = String.format('{0}/config/getConfig', this.GLOBAL.WEBUI_URL);
      this.axios
        .get(url)
        .then(response => {
          this.browserValue = response.data.data.headless;
          this.status = this.browserValue === '1';
          return response;
        })
        .catch(response => {
          return response;
        });
    },
    getDebugValue() {
      let url = String.format('{0}/debugCase/getStatus', this.GLOBAL.WEBUI_URL);
      this.axios
        .get(url)
        .then(response => {
          this.debugStatus = response.data.data.status;
          return response;
        })
        .catch(response => {
          return response;
        });
    },
    updateDebugHub() {
      let config = {
        headers: {
          'Content-Type': 'application/json;charset=UTF-8'
        }
      };
      let url = String.format(
        '{0}/debugCase/updateDebugHub',
        this.GLOBAL.WEBUI_URL
      );

      this.axios
        .post(
          url,
          JSON.stringify({ status: this.debugStatus ? '1' : '0' }),
          config
        )
        .then(response => {
          this.getDebugValue();
          return response;
        })
        .catch(response => {
          return response;
        });
    },
    getLanguageValue() {
      let url = String.format('{0}/config/getLanguage', this.GLOBAL.WEBUI_URL);
      this.axios
        .get(url)
        .then(response => {
          this.languageValue = response.data.data.language;
          this.languageStatus = this.languageValue === '1';
          return response;
        })
        .catch(response => {
          return response;
        });
    },
    getMessageConfig() {
      let url = String.format(
        '{0}/config/getMessageConfig',
        this.GLOBAL.WEBUI_URL
      );
      this.axios
        .get(url)
        .then(response => {
          this.messageConfig = response.data.data.messageConfig;
          return response;
        })
        .catch(response => {
          return response;
        });
    },
    updateMessageConfig() {
      this.$refs['messageConfig'].validate(valid => {
        if (valid) {
          let config = {
            headers: {
              'Content-Type': 'application/json;charset=UTF-8'
            }
          };
          let url = String.format(
            '{0}/config/updateMessageConfig',
            this.GLOBAL.WEBUI_URL
          );
          this.axios
            .post(url, JSON.stringify(this.messageConfig), config)
            .then(response => {
              if (response.data.code === '1000') {
                this.$message({
                  type: 'success',
                  message: response.data.msg
                });
              } else {
                this.$message({
                  type: 'error',
                  message: response.data.msg
                });
              }

              this.getMessageConfig();
              return response;
            })
            .catch(response => {
              return response;
            });
        } else {
          this.$message({
            type: 'error',
            message: '请填写必填项!'
          });
        }
      });
    },
    updateBrowserValue() {
      let config = {
        headers: {
          'Content-Type': 'application/json;charset=UTF-8'
        }
      };
      this.browserValue = this.status ? '1' : '0';
      let url = String.format('{0}/config/updateConfig', this.GLOBAL.WEBUI_URL);
      this.axios
        .post(url, JSON.stringify({ headless: this.browserValue }), config)
        .then(response => {
          this.getBrowserValue();
          return response;
        })
        .catch(response => {
          return response;
        });
    },
    updateLanguageValue() {
      let config = {
        headers: {
          'Content-Type': 'application/json;charset=UTF-8'
        }
      };
      this.languageValue = this.languageStatus ? '1' : '0';
      let url = String.format(
        '{0}/config/updateLanguage',
        this.GLOBAL.WEBUI_URL
      );
      this.axios
        .post(url, JSON.stringify({ language: this.languageValue }), config)
        .then(response => {
          this.getLanguageValue();
          return response;
        })
        .catch(response => {
          return response;
        });
    }
  },

  beforeMount() {
    this.getBrowserValue();
    this.getLanguageValue();
    this.getDebugValue();
    this.getWindowSize();
    this.getMessageConfig();
  }
};
</script>

<style scoped>
.el-row {
  line-height: 40px;
  margin-top: 10px;
}
</style>



